<template>
  <div>
    <!-- 头部组件 -->
    <app-header>
      <template>
        <div slot="title">修改个人信息</div>
        <div slot="right" @click="userEdit" v-if="tag=='nickname'">保存</div>
        <div slot="right" @click="sexEdit" v-if="tag=='sex'">保存</div>
        <div slot="right" @click="subjectsEdit" v-if="tag=='subjects'">保存</div>
      </template>
    </app-header>
    <!-- 用户名修改 -->
    <van-cell-group v-if="tag=='nickname'" style="margin-top:0.1rem">
      <van-field v-model="nickname" placeholder="请输入用户名" />
    </van-cell-group>
    <!-- 性别修改 -->
    <ul class="content" v-if="tag=='sex'">
      <li :class="active==0?'active':''" @click="active=0">男</li>
      <li :class="active==1?'active':''" @click="active=1">女</li>
    </ul>
    <!-- 学科修改 -->
    <div v-if="tag=='subjects'">
      <p class="p-box" v-for="item in subjects" :key="item.id">
        <input
          type="checkbox"
          :checked="str.includes(item.id)"
          @click="changeUpdate(item.id,$event)"
        />
        <span>{{ item.name}}</span>
      </p>
    </div>
  </div>
</template>

<script>
import { AjaxEditUser, AjaxEditAttribute } from "../../utils/myApi";
import appHeader from "../../components/AppHeader.vue";
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: { appHeader },
  // 组件状态值
  data() {
    return {
      active: this.$route.query.value,
      nickname: this.$route.query.value,
      attr: this.$route.query.value,
      tag: this.$route.query.tag,
      subjects: [],
      id: [],
      user_attr: [],
      str: ""
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 用户名修改
    async userEdit() {
      await AjaxEditUser({ nickname: this.nickname });
      this.$router.push("/info");
      this.$toast.success("修改成功");
    },
    // 性别修改
    async sexEdit() {
      await AjaxEditUser({ sex: this.active });
      this.$router.push("/info");
      this.$toast.success("修改成功");
    },
    // 学科修改
    async subjectsEdit() {
      await AjaxEditUser({ user_attr: JSON.stringify(this.user_attr) });
      this.$router.push("/info");
      this.$toast.success("修改成功");
    },
    // 学科选中状态
    changeUpdate(id, e) {
      if (e.target.checked) {
        let obj = { attr_id: 2, attr_val_id: id };
        let index = this.user_attr.findIndex(item => {
          return item == obj;
        });
        if (index == -1) {
          this.user_attr.push(obj);
        }
      } else {
        let index = this.user_attr.findIndex(item => {
          return item.attr_val_id == id;
        });
        this.user_attr.splice(index, 1);
      }
    },
    // 学科默认渲染
    async subjectsA() {
      let { data } = await AjaxEditAttribute();
      this.subjects = data[1].value;
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  mounted() {
    // 学科默认渲染调用
    this.subjectsA();
    console.log(this.attr);
    this.attr.map(item => {
      // 学科默认
      if (item.attr_id == 2) {
        this.user_attr.push({ attr_id: 2, attr_val_id: item.attr_val_id });
        this.str += item.attr_val_id;
      } else {
        this.user_attr.push({ attr_id: 1, attr_val_id: item.attr_val_id });
      }
    });
    console.log(this.user_attr);
  }
};
</script> 

<style scoped lang="scss">
.content {
  background: #fff;
  margin: 2.66667vw 0;
  padding: 1.33333vw 4vw;
  background-size: 0.4rem 0.4rem;
  li {
    height: 14.66667vw;
    line-height: 14.66667vw;
    font-size: 3.73333vw;
    color: #595959;
    position: relative;
  }
  .active {
    background: url(data:image/png;base64,)
      no-repeat 100%;
    background-size: 4vw 4vw;
  }
}
.p-box {
  width: 100%;
  background-color: #fff;
  height: 0.55rem;
  padding: 0 0.15rem;
  box-sizing: border-box;
  display: flex;
  input {
    margin: 0.15rem 0.1rem 0.03rem 0.04rem;
    width: 0.25rem;
    height: 0.25rem;
  }
  span {
    height: 0.55rem;
    display: inline-block;
    line-height: 0.55rem;
    font-size: 0.16rem;
  }
}
</style>
